import React, { useState } from 'react';
import PropTypes from 'prop-types';

import HeaderContainer from 'Common/Components/Header/HeaderContainer';
import FooterContainer from 'Common/Components/Footer/FooterContainer';
import AlarmSymbol from '@webui-one/styles/static/AlarmSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Loader from '@webui-one/components/Loading/Loader';
import ErrorBox from '@webui-one/components/ConfigRender/ErrorBox';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import NotificationBar, { BAR_TYPES } from '@webui-one/components/Notification/NotificationBar';
import style from './HighAvailabilityStyles';
import Logo from './static/Logo';
import {
    MODE_MASTER,
    MODE_NONE,
    MODE_SLAVE,
} from './constants';

const getColor = (configuredAs, runningAs) => {
    if (runningAs === MODE_MASTER) {
        return configuredAs === MODE_MASTER
            ? style.gold
            : style.silver;
    }
    return defaultStyles.color3;
};

const getBackgroundColor = (runningAs) => {
    if (runningAs === MODE_MASTER || runningAs === MODE_SLAVE) return style.active;
    return style.inactive;
};

const getCompatibilityColor = (member1, member2) => {
    if (member1.name === member2.name) {
        return style.active;
    }
    if (member1.packagesHash === member2.packagesHash
        && member1.cdbHash === member2.cdbHash) {
        return defaultStyles.color14;
    }
    return defaultStyles.color5;
};

