const getTiles = (tileData, compareTo) =>
    // const hashGroups = createHashGroups(tileData);
    tileData.map((tile, idx) => (
        <div
            key={`tile_${idx}`}
            data-handle={`tile-${tile.name}`}
            style={style.tile}
        >
            {tile.isCurrent && <div style={style.label} data-handle="current">current</div>}
            <div style={style.title}>{tile.name}</div>
            <div style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
                <div style={style.sideTextContainer}>
                    {/* <div style={style.text}>pkg compatibility:</div>
                    <div style={style.text2}>{hashGroups[tile.packagesHash]}</div>
                    <div style={style.text}>CDB compatibility:</div>
                    <div style={style.text2}>{hashGroups[tile.cdbHash]}</div> */}
                    <div style={style.text}>running as:</div>
                    <div style={style.text2} data-handle="running-as">{tile.runningAs}</div>
                </div>
                <div style={style.logo}>
                    <Logo
                        color={getColor(tile.configuredAs, tile.runningAs)}
                        backgroundColor={compareTo ?
                            getCompatibilityColor(tile, compareTo) :
                            getBackgroundColor(tile.runningAs)
                        }
                    />
                </div>
                <div style={style.sideTextContainer}>
                    <div style={style.text}>configured as:</div>
                    <div style={style.text2} data-handle="configured-as">{tile.configuredAs}</div>
                </div>
            </div>
            {tile.found || (
                <div data-handle="not-found" style={style.notFoundOverlay}>
                    <div style={style.warningSymbol}>
                        <AlarmSymbol color={defaultStyles.color5} />
                    </div>
                    <div style={style.warningText}>NOT FOUND</div>
                </div>
            )}
            {(compareTo && tile.name === compareTo.name) && (
                <div style={style.selectedTile} />
            )}
        </div>
    ));

const getMessage = masterId => (
    `Current node is a slave node, status of other nodes are unavailable.\
    Go to master node ${masterId} to see status of all available nodes.`
);

const HighAvailability = ({
    assignedRole,
    enabled,
    error,
    isLoading,
    masterId,
    members,
}) => {
    const [isPresentationMode, setIsPresentationMode] = useState(true);
    const [selectedName, setSelectedName] = useState(
        members.length > 0 ? members[0].name : '',
    );
    return (
        <div style={style.application}>
            <HeaderContainer>
                <div style={style.secondHeader}>
                    <div style={style.controls}>
                        <CheckBox
                            styleName={'radio'}
                            value={isPresentationMode}
                            onSetValue={() => setIsPresentationMode(true)}
                        >
                            <div style={style.radioButtonLabel}>show current state</div>
                        </CheckBox>
                        <CheckBox
                            styleName={'radio'}
                            value={!isPresentationMode}
                            onSetValue={() => setIsPresentationMode(false)}
                        >
                            <div style={style.radioButtonLabel}>show compatibility with:</div>
                        </CheckBox>
                        <select
                            style={style.select}
                            disabled={isPresentationMode}
                            onChange={event => (setSelectedName(event.target.value))}
                        >
                            {members.map((member, i) =>
                                <option value={member.name} key={i}>{member.name}</option>,
                            )}
                        </select>
                    </div>
                </div>
                <div style={defaultStyles.shadow} />
            </HeaderContainer>
            {assignedRole === MODE_SLAVE && (
                <div>
                    <NotificationBar
                        message={getMessage(masterId)}
                        additionalStyles={{ textAlign: 'center' }}
                        type={BAR_TYPES.WARNING}
                    />
                    <div style={defaultStyles.shadow} />
                </div>
            )}
            <div style={style.scrollingContainer}>
                <div style={style.widgetsContainer}>
                    {isLoading && (
                        <div style={style.loader}>
                            <Loader color={defaultStyles.color9} />
                        </div>
                    )}
                    {error && <ErrorBox error={error} />}
                    {enabled === false && (
                        <div
                            data-handle="not-enabled"
                            style={{ margin: '2rem', color: defaultStyles.color7 }}
                        >
                            High availability is not enabled
                        </div>
                    )}
                    {getTiles(members, isPresentationMode ? null :
                    members.find(member => member.name === selectedName))}
                </div>
            </div>
            <FooterContainer />
        </div>
    );
};

HighAvailability.propTypes = {
    assignedRole: PropTypes.string,
    enabled: PropTypes.bool,
    error: PropTypes.shape(Object),
    isLoading: PropTypes.bool,
    masterId: PropTypes.string,
    members: PropTypes.arrayOf(PropTypes.shape({
        configuredAs: PropTypes.string,
        found: PropTypes.bool.isRequired,
        isCurrent: PropTypes.bool,
        name: PropTypes.string.isRequired,
        runningAs: PropTypes.string,
    })),
};

