import { useCallback } from 'react';
import { useFetch } from '@webui-one/components/hooks/useFetch';
import {
    getLastTh as _getLastTh,
    showConfig as _showConfig,
} from '@webui-one/core/jsonrpc';

export default function useGetHighAvailability({
    trigger,
    funcs: {
        getLastTh = _getLastTh,
        showConfig = _showConfig,
    } = {},
} = {}) {
    const asyncFunc = useCallback(async ({ signal }) => {
        const th = await getLastTh({ signal });
        return showConfig({
            th,
            path: '/ncs:high-availability',
            resultAs: 'json',
            withOper: true,
            signal,
        });
        // eslint-disable-next-line
    }, [getLastTh, showConfig, trigger]);

    return useFetch({ asyncFunc });
}
