import React, { useEffect, useReducer } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import _useGetHighAvailability from './useGetHighAvailability';
import HighAvailability from './HighAvailability';
import {
    flagTypes as coreFlagTypes,
    flagIsSet as _flagIsSet,
} from '@webui-one/core/flags';
import { isDevEnv as _isDevEnv } from '@webui-one/core/utils/platform';
import {
    subscribeChangesAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import {
    MODE_MASTER,
    MODE_SLAVE,
    mockDataMaster,
} from './constants';

const REFRESH_HA_STATUS_INTERVAL = 5000;

export const shouldUseMockedHa = ({
    funcs: {
        flagIsSet = _flagIsSet,
        isDevEnv = _isDevEnv,
    } = {},
} = {}) => (
    isDevEnv()
    && flagIsSet(coreFlagTypes.SHOULD_USE_MOCKED_HA, 'true')
);

const getRunningAs = (status, id) => {
    if (id === status['current-id']) return status.mode;
    if (Array.isArray(status['connected-slave']) && status['connected-slave'].some(slave => id === slave.id)) {
        return MODE_SLAVE;
    }
    if (status.mode === MODE_SLAVE) {
        if (id === status['master-id']) return MODE_MASTER;
    }

    return 'unknown';
};

export const getNodes = (data) => {
    const isCurrentMaster = data?.status?.mode === MODE_MASTER;
    return data['ha-node'].reduce((acc, cur) => {
        const runningAs = getRunningAs(data.status, cur.id);
        acc.push({
            configuredAs: cur['nominal-role'] ? cur['nominal-role'] : 'not set',
            found: !(runningAs === 'unknown' && isCurrentMaster),
            isCurrent: cur.id === data.status['current-id'],
            name: cur.id,
            runningAs,
        });
        return acc;
    }, []);
};

export const getStatus = (data, key) => {
    if (!data) return undefined;
    if (!data.status) return undefined;
    return data.status[key];
};

const HighAvailabilityContainer = ({
    shouldUseMockedData,
    useGetHighAvailability,
    onSubscribe,
    onUnsubscribe,
}) => {
    const [triggerIndex, triggerChanges] = useReducer(i => i + 1, 0);
    const { isLoading, data: rawData, error } = useGetHighAvailability({ trigger: triggerIndex });

    const data = shouldUseMockedData() ? mockDataMaster : rawData;
    const haData = data && data.data && data.data['tailf-ncs:high-availability'];
    const enabled = haData && haData.enabled;
    const assignedRole = getStatus(haData, 'assigned-role');
    const masterId = getStatus(haData, 'master-id');
    const nodes = enabled ? getNodes(haData) : [];

    useEffect(() => {
        onSubscribe(triggerChanges);

        return () => {
            onUnsubscribe(triggerChanges);
        };
    }, [onSubscribe, onUnsubscribe, triggerChanges]);

    useEffect(() => {
        const intervalEvent = setInterval(triggerChanges, REFRESH_HA_STATUS_INTERVAL);

        return () => {
            clearInterval(intervalEvent);
        };
    }, [triggerChanges]);

    return (
        <HighAvailability
            assignedRole={assignedRole}
            masterId={masterId}
            enabled={enabled}
            members={nodes}
            isLoading={isLoading}
            error={error}
        />
    );
};

HighAvailabilityContainer.propTypes = {
    shouldUseMockedData: PropTypes.func,
    useGetHighAvailability: PropTypes.func,
    onSubscribe: PropTypes.func,
    onUnsubscribe: PropTypes.func,
};

HighAvailabilityContainer.defaultProps = {
    shouldUseMockedData: shouldUseMockedHa,
    useGetHighAvailability: _useGetHighAvailability,
};

const highAvailabilityPath = [
    '/ncs:high-availability',
];

const mapDispatchToProps = dispatch => ({
    onSubscribe(handler) {
        dispatch(subscribeChangesAction(highAvailabilityPath, handler));
    },
    onUnsubscribe(handler) {
        dispatch(unsubscribeChangesAction(highAvailabilityPath, handler));
    },
});

export default connect(
    undefined,
    mapDispatchToProps,
)(HighAvailabilityContainer);

export {
    HighAvailabilityContainer as _HighAvailabilityContainer,
};
